<template>
  <div class="example">
    {{msg}}
    <el-button @click="visible = true">按钮</el-button>
    <el-dialog :visible.sync="visible" title="Hello world">
      <p>欢迎使用 Element</p>
    </el-dialog>

    <child v-bind:message="childMsg" v-on:getChildMsg="showMsgFromChild"></child>
    <!-- <h4>{{childMsg}}</h4> -->
  </div>
</template>
<script>
import Child from '@/views/example/components/child'
export default {
  name: 'Example',
  components: {
    Child
  },
  data () {
    return {
      msg: '只有诚实的人才能看到我',
      visible: false,
      childMsg: '1'
    }
  },
  methods:{
    showMsgFromChild (data) {
      console.log(data, this.childMsg );
      this.childMsg = data
    }
  }
}
</script>


