<template>
  <div id="child">
    <h2>介是一个子组件 {{ message }}</h2>

    <el-button @click="testClick">按一哈</el-button>
  </div>
</template>

<script>
export default {
  name: 'child',
  props:['message'],
  data () {
    return {
     
    }
  },
  methods: {
    testClick ()  {
      let message = parseInt(this.message) + 1
      this.$emit("getChildMsg",message)
    }
  },
  created () {
    console.log(this.message)
  }

}
</script>



